<template>
  <div class="more">
    <div class="page-header-main">
      <mt-header>
        <a @click="back" slot="left">
          <mt-button icon="back">设置</mt-button>
        </a>
      </mt-header>
    </div>
    <div class="moreBody">
      <mt-cell @click.native="goChangePasswd" title="修改密码"></mt-cell>
      <mt-cell title="关于我们"></mt-cell>
      <mt-cell @click.native="feedback" title="反馈建议"></mt-cell>
    </div>
    <div class="footer">
      <mt-button @click.native="loggout" size="large">退出登录</mt-button>
    </div>
  </div>
</template>

<script>
import { mapGetters, mapActions} from 'vuex'
export default {
  name: 'more',
  data () {
    return {
    }
  },
  methods: {
    ...mapActions({
      changeToken: 'changeToken',
      changeAvatar: 'changeAvatar',
      changePhone: 'changePhone',
      changeTabSelected: 'changeTabSelected'
    }),
    back: function() {
      this.$router.push('/index/mymenu');
    },
    feedback: function() {
      this.$router.push('/feedback');
    },
    goChangePasswd: function() {
      this.$router.push('/changePasswd');
    },
    loggout: function() {
      sessionStorage.setItem('token', null);
      this.changeToken('');
      this.changeAvatar('');
      this.changePhone('');
      this.$router.push('/');
    }
  }
}
</script>
<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>
.more .mint-header {
  background-color: #fff;
  color: #000000;
  padding-top: 11px;
}
.more .mint-header label {
  margin-left: 2%;
}
.forgetPasswd .mint-button::after {
  background-color: #f1cfde;
}
.more .mint-cell-text {
  font-size: 0.4rem;
}
.more .mint-button-text {
  font-size: 0.4rem;
  display: inline-block;
  padding-top: 10px;
}
.more .mint-button {
  height: 1rem;
}
.more .mintui {
  font-size: 0.4rem;
}
.more .mint-header {
  font-size: 0.4rem;
}
.more .mint-cell {
  min-height: 1.5rem;
}
.more .moreBody {
  margin-top: 5%;
}
.more .mint-header {
  height: 1rem;
  line-height: 1rem;
  background: #fff;
  color:#000000;
}
.more .mint-button {
  height: 1rem;
}
.more  .mintui {
  font-size: 0.5rem;
}
.more .mint-button-icon {
  vertical-align: middle;
}
.more .mint-button-text {
  font-size: 0.5rem;
  vertical-align: middle;
}
.more .mint-header-button.is-right{
  color: #d06091;
}
.footer {
    right: 0;
    bottom: 0;
    left: 0;
    position: fixed;
    z-index: 1;
}
.footer .mint-button .mint-button-text {
  height: 1rem;
  font-size: 0.5rem;
  padding-top: 20px;
}
</style>



